@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: 'Rouble';

  font-display: swap;
  src: url('../fonts/rouble.woff2'), url('../fonts/rouble.woff');
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: 'Bebas-Neue-Regular';

  font-display: swap;
  src: local('Bebas-Neue-Regular'), url('../fonts/BebasNeueRegular.woff2'),
    url('../fonts/BebasNeueRegular.woff');
}

@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: 'Roboto-Condensed-Light';

  font-display: swap;
  src: local('Roboto-Condensed-Light'),
    url('../fonts/RobotoCondensed-Light.woff2'),
    url('../fonts/RobotoCondensed-Light.woff');
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: 'Roboto-Condensed-Regular';

  font-display: swap;
  src: local('Roboto-Condensed-Regular'),
    url('../fonts/RobotoCondensed-Regular.woff2'),
    url('../fonts/RobotoCondensed-Regular.woff');
}

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: 'Roboto-Condensed-Bold';

  font-display: swap;
  src: local('Roboto-Condensed-Bold'),
    url('../fonts/RobotoCondensed-Bold.woff2'),
    url('../fonts/RobotoCondensed-Bold.woff');
}
